				v-if="tableName=='yonghu'" class="uni-form-item uni-column">
				<input
					:style='{"padding":"0 24rpx","boxShadow":"0 2rpx 12rpx rgba(0,0,0,0)","margin":"0","borderColor":"rgba(164, 164, 164, 1)","backgroundColor":"#fff","color":"#333","textAlign":"left","borderRadius":"40rpx","borderWidth":"2rpx","width":"100%","fontSize":"28rpx","borderStyle":"solid","height":"80rpx"}'
					v-model="ruleForm.mima2" type="password" class="uni-input" name="" placeholder="确认密码" />
